<template>
    <div class="index">
        <div class="logo" :style="{backgroundImage: 'url('+ logo +')'}"></div>
        <div class="img_list">
            <div class="dog" :style="{backgroundImage: 'url('+ dog +')'}"></div>
            <span>点亮</span>
            <div class="heart"></div>
            <span>动值</span>
            <div class="tmall" :style="{backgroundImage: 'url('+ tmall +')'}"></div>
        </div>
        <div class="text">助力<span>七色纺天猫旗舰店</span>上线</div>
        <div class="text2"></div>
        <div class="wallet"></div>
        <div class="open_btn"></div>
    </div>
</template>

<script>
import logo from 'static/img/logo.png'
import dog from 'static/img/dog77.png'
import tmall from 'static/img/tmall.png'
export default {
  name: 'Index',
  data () {
    return {
      logo: logo,
      dog: dog,
      tmall: tmall
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less" rel="stylesheet/less">
.index {
    overflow: hidden;
    .logo {
        width: 271px;
        height: 103px;
        margin: 20px 0 0 8px;
    }
    .img_list {
        display: flex;
        margin: -20px 0 20px 106px;
        span {
            color: #ffffff;
            font-size: 38px;
            line-height: 195px;
        }
        span:nth-child(2) {
            margin: 0 15px 0 -20px;
        }
        span:nth-child(4) {
            margin: 0 2px 0 10px;
        }
        .dog {
            width: 157px;
            height: 145px;
        }
        .heart {
            width: 112px;
            height: 90px;
            background-image: url('heart.png');
            margin-top: 55px;
        }
        .tmall {
            width: 122px;
            height: 141px;
            margin-top: 7px;
        }
    }
    .text {
        color: #ffffff;
        font-size: 34px;
        text-align: center;
        span {
            font-size: 54px;
        }
    }
    .text2 {
        width: 569*0.95px;
        height: 162*0.95px;
        background-image: url('text.png');
        margin: 0 auto;
        margin-top: 62px;
    }
    .wallet {
        width: 544*0.95px;
        height: 438*0.95px;
        margin: 0 auto;
        margin-top: -8px;
        transform: translateX(30px);
        background-image: url('wallet.png');
    }
    .open_btn {
        width: 347px;
        height: 161px;
        background-image: url('open_btn.png');
        margin: 0 auto;
        margin-top: 4px;
    }
}
</style>



