<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  mounted () {
    this.$router.push('/Index')
  }
}
</script>

<style scoped lang="less" rel="stylesheet/less">
#app {
    height: 100%;
    width: 100%;
    box-sizing: border-box;
    position: relative;
}
</style>



